@use '../base/variables' as v;
@use '../base/mixins' as m;

.tabs {

    @include m.tablet {
        display: flex;
    }

    button {
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 3rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color 200ms ease;

        &:hover {
            cursor: pointer;
            background-color: v.$secundario;
        }

        &.actual {
            background-color: v.$secundario;
            // color: v.$azul;
        }
    }
}